var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('v-container',[_c('h1',[_vm._v("月光骑士")]),_c('hr',{staticClass:"mt-3 v-divider theme--light"}),_c('div',{staticStyle:{"display":"flex","flex-direction":"row"}},[_c('div',{staticStyle:{"display":"flex","flex-direction":"column"}},[_c('blockquote',{staticClass:"blockquote"},[_vm._v("        月亮骑士（Marc Spector）是一部虚构的 超级英雄，出现在由漫威漫画公司出版的美国漫画书中。由Doug Moench和Don Perlin创作，这个角色最早出现在夜晚＃32（1975年8月）的狼人身上。")]),_c('blockquote',{staticClass:"blockquote"},[_vm._v("        这个角色在狼人夜晚＃32（1975年8月）首次亮相，由道格·莫恩奇（Doug Moench）和唐·佩林（Don Perlin）的艺术作品一起出演，作为委员会雇佣的一个反派角色，在＃33中继续讲述他们的两个部分。他发现委员会想用他作为武器，帮助狼人逃跑，与他并肩作战。月亮骑士后来以恶魔幻影的形式回归，在37号（1976年3月）出现，再次与狼人作战。编辑Marv Wolfman和Len Wein喜欢这个角色，促使他们在漫威聚光灯＃28-29（1976年6月/ 8月），中授予他独奏地点。Doug Moench再次撰写Don Perlin的艺术作品。这个故事，以及Bill Mantlo写的壮观蜘蛛侠＃22和＃23（1978年9月/ 10月），将月亮骑士重新塑造为英雄，并以其恶毒的第一次出现作为掩护渗入委员会。随后出现在Marvel二合一＃52中，由史蒂文格兰特写的艺术作品由Jim Craig和The Defenders＃47-51 编写，让他在与Zodiac Cartel的战争期间短暂地加入了Defenders。")])]),_c('v-img',{staticClass:"stage-img",attrs:{"width":"195","height":"260","src":""}})],1),_c('br'),_c('h2',[_vm._v("创作背景")]),_c('hr',{staticClass:"mt-3 v-divider theme--light"}),_c('br'),_c('div',{staticStyle:{"display":"flex","flex-direction":"row"}},[_c('div',{staticStyle:{"display":"flex","flex-direction":"column"}},[_c('blockquote',{staticClass:"blockquote",staticStyle:{"margin-top":"-20px"}},[_vm._v("        月亮骑士然后在绿巨人中获得了一个备用带！杂志＃11-15，＃17-18和＃20，在第13-15,17-18和20号问题上首次与艺术家Bill Sienkiewicz合作，以及杂志中的黑白故事出版Marvel Preview＃21。Sienkiewicz的Neal Adams影响了艺术风格，有助于巩固月亮骑士仅仅是蝙蝠侠克隆的早期感知。的绿巨人备份和奇迹预览的问题，这都写的道格·莫奇，提供月光骑士有局部的起源故事，并介绍了他最显着的经常性的恶棍之一：兰德尔·斯佩克特，谁以后会成为暗影骑士。")]),_c('blockquote',{staticClass:"blockquote",staticStyle:{"margin-top":"-20px"}},[_vm._v("        Moon Knight于1980年收到了他的第一个正在进行的系列，Doug Moench和Bill Sienkiewicz是其主要的创作团队。这个角色收到了一个完整的起源故事，他的大多数着名的反复出现的恶棍都被介绍了; 特别是他的敌人布什曼。这本书的早期销售情况良好，导致Marvel在第15期发行，将报刊从报刊发行中分离出来，并使其成为仅在漫画店中出售的一组书籍的旗舰产品之一。还推出了一个伴侣迷你系列，月亮骑士：特别版，重印了绿巨人和漫威预览版月亮骑士故事的颜色和漫画形式，而不是他们的原始杂志格式。Sienkiewicz在第30期之后离开了系列剧，但在最后一期（＃38）之前继续提供封面。")])]),_c('v-card',{staticClass:"card-img"},[_c('v-img',{attrs:{"width":"180","height":"240","src":""}}),_c('v-card-title',{attrs:{"primary-title":""}},[_c('div',[_c('span')])])],1)],1),_c('br')])}
